
import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCarousel,
  CCarouselCaption,
  CCarouselControl,
  CCarouselIndicators,
  CCarouselInner,
  CCarouselItem,
  CInputGroup,
  CSelect,
  CInputGroupPrepend,
  CInputGroupText,
  CCol,
  CContainer,
  CRow, 
  CLink,
  CButton
} from '@coreui/react'
import CIcon from '@coreui/icons-react';
import C9 from "../../assets/img/c10.jpg";
import f1 from "../../assets/img/cash.jpg";
import f2 from "../../assets/img/h2.jpg";
import f3 from "../../assets/img/h3.jpg";
import Card from '../component/Card';
import Subcate from "../component/Subcate";
import CateCard from '../component/CateCard'
import "../../scss/Card.scss";
import { faStreetView,faCarAlt, faBirthdayCake, faHamburger, faUtensilSpoon, faUtensils, faVestPatches, faDonate, faPumpMedical, faMobileAlt, faEye, faShoppingCart, faBackspace, faBackward, faUndoAlt} from '@fortawesome/free-solid-svg-icons';
import { faArrowAltCircleLeft, faShoppingBasket, faTv } from '@fortawesome/fontawesome-free-solid';
import { Link } from 'react-router-dom';
import { Category } from '@material-ui/icons';
import {ToastsContainer, ToastsStore,ToastStore,ToastsContainerPosition} from "react-toasts";
import Vendor from '../component/Vendor';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import  ProductList from "../component/ProductList";
import Nearby from '../component/Nearby';
import {Base_URL} from "../../CommonRoutes/Api"

class Location extends Component {
  constructor(){
    super()
    this.state={
      all:true,
      cate:false,
      category:[],
      isCity:"",
      cityname:[],
      cityError:"",
      subcate:false,
      subcategory:[],
      vendor:false,
      product:false,
      productlist:[],
      vendorlist:[],
      nearby:false,
      shoplist:[],
      lat:"",
      long:"",

    }
   /*  if (Platform.OS === 'android') {
      this.getPermissions()
  } else {
      this.findCoordinates()
  } */
  }

 /*  getPermissions = () => {
    RNAndroidLocationEnabler.promptForEnableLocationIfNeeded({interval: 10000, fastInterval: 5000})
    .then(data => {
      if (data === "already-enabled") {
        this.findCoordinates()
      } else {
        setTimeout(() => {
          this.findCoordinates()
        }, 1000)
      }
    })
  }; */

   valid() { 

       if(this.state.isCity.length<1)
        {
             this.setState(  
              ToastsStore.error("Enter city name")
                
                
                 )
                 return false
        } else{
          return true;
        }
      }
  componentDidMount() {


    const location = window.navigator && window.navigator.geolocation
    if (location) {
      location.getCurrentPosition((position) => {
        // console.log(position)
        this.setState({
          lat: position.coords.latitude,
          long: position.coords.longitude,
        })

        fetch('https://maps.googleapis.com/maps/api/geocode/json?address=' + this.state.lat + ',' + this.state.long + '&key=')
        .then((response) => response.json())
        .then((responseJson) => {
            console.log('ADDRESS GEOCODE is BACK!! => ' + JSON.stringify(responseJson));
})
        }, (error) => {
        this.setState({ lat: 'err-latitude', long: 'err-longitude' })
      })
    }
    
      
    
    

    fetch( Base_URL + "allCitys").then((response) => {
      response.json().then((result) => {
        console.log(result);
        this.setState({ cityname: result })
      })
    })
   
}
    
  handleCat(){

    
  {    //console.log(this.state);
    this.setState(
     {cityError:""}
    )
     
    if(this.valid()){   
    this.setState({all:false,cate:true})
    fetch(Base_URL + "allCategories").then((response) => {
      response.json().then((result) => {
        console.log(result);
        this.setState({category : result })
      })
    })
  }

  } 
}

handleSubcat(id){
  this.setState({all:false,cate:false,subcate:true})
  fetch(Base_URL + "getSubCategorybycatid/" +id).then((response) => {
    response.json().then((result) => {
      console.log( "sub",result);
      this.setState({subcategory : result })
    })
  })

}
handleSubcategory(id){
  this.setState({all:false,cate:false,subcate:true})
  fetch(Base_URL + "getSubCategorybycatid/" +id).then((response) => {
    response.json().then((result) => {
      console.log( "sub",result);
      this.setState({subcategory : result })
    })
  })

}

back(){
  this.setState({all:true,cate:false})
}

backsub(){
  this.setState({all:true,subcate:false})
}

backvendor(){
  this.setState({subcate:true,vendor:false})
}


handleVendor(id){
  //this.setState({isCity:e.target.value})
  //console.log(this.state.isCity);
  this.setState({all:false,cate:false,subcate:false,vendor:true})
  fetch(Base_URL + "getvendorbysubcatid/" +id).then((response) => {
    response.json().then((result) => {
      console.log( "vendor",result);
      this.setState({vendorlist : result })
    })
  })

}

handleproduct(id){
  //this.setState({isCity:e.target.value})
  //console.log(this.state.isCity);
  this.setState({all:false,cate:false,subcate:false,vendor:false,nearby:false,product:true})
  fetch(Base_URL + "getproducts_vendor_id/" +id).then((response) => {
    response.json().then((result) => {
      console.log( "product",result);
      this.setState({productlist : result })
    })
  })

}




handleNearby(){  
    //this.getMyLocation();
     this.setState({all:false,cate:false,subcate:false,vendor:false,nearby:true})
  
   fetch( Base_URL + "findvendor/" +this.state.lat +"/"+ this.state.long) .then((response) => {
    response.json().then((result) => {
      console.log( "shoplist",result);
      this.setState({shoplist:result})
     
  }) 
  
 
}) 

}


  render() {
    return (
      <div>
      <ToastsContainer position={ToastsContainerPosition.TOP_RIGHT} store={ToastsStore}/>
      { this.state.all ?
         <CContainer>
         <CRow>
         <CCol xs="12" xl="12" sm="12">
        <CCard>
          <CCardBody  className="Ccard_dash">
            <CCarousel animate autoSlide={3000}>
              <CCarouselIndicators/>
              <CCarouselInner>
                <CCarouselItem>
                  <img className="d-block w-100" src={C9} width="400px" height="150px" alt="slide 1"/>
                  <CCarouselCaption></CCarouselCaption>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={f1} width="400px" height="150px" alt="slide 2"/>
                  <CCarouselCaption></CCarouselCaption>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={f2} width="400px" height="150px" alt="slide 3"/>
                  <CCarouselCaption></CCarouselCaption>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={f3} width="400px" height="150px" alt="slide 4"/>
                  <CCarouselCaption></CCarouselCaption>
                </CCarouselItem>
              </CCarouselInner>
              <CCarouselControl direction="prev"/>
              <CCarouselControl direction="next"/>
            </CCarousel>
          </CCardBody>
          <CInputGroup className="mb-12">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-location-pin" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CSelect value=""
                      id="select" selected
                      value={this.state.isCity}
                      onChange={(event) => { this.setState({ isCity: event.target.value }) }}
                      name="cityname">
                      <option>select city</option>

                       { this.state.cityname.length >0 ?
                        this.state.cityname.map((item, i) =>
                          <option value={item.city_name}

                          
                          >{item.city_name}
                          </option>

                        ):'No City Found'} 
                        
                    </CSelect>
                  </CInputGroup>
                  <div> {this.state.cityError ?(
                        <p style={{color:"red"}}> {this.state.cityError}</p>):null } </div>
        </CCard>
        </CCol>
      </CRow>
      <CRow>
      <CCol xs="12" sm="12" md="12" className="card_col">
      </CCol>
      </CRow>
      <CRow>
         <CCol xs="4" sm="6" md="4" className="card_col">
         
         <Link href="#"
     onClick={()=>this.handleNearby()}
    > <Card src={faStreetView} heading="Nearyby me" />
       </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory(3)}>
        <Card src={faCarAlt} heading="Automobile sale" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4"  className="card_col" >
        <Link href="#" onClick={()=>this.handleSubcategory(4)}>
        <Card src={faBirthdayCake} heading="Cake Bakery" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4"  className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory(5)}>
        <Card src={faHamburger} heading="Catering Services" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory(6)}>
        <Card src={faUtensils} heading="Dhaba & Restaurent" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col" >
        <Link href="#" onClick={()=>this.handleSubcategory(7)}>
        <Card src={faTv} heading="Electonics Home Ap" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4"  className="card_col" >
        <Link href="#" onClick={()=>this.handleSubcategory(8)}>
        <Card src={faVestPatches} heading="Fashion Wear" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col" >
        <Link href="#" onClick={()=>this.handleSubcategory(9)}>
        <Card src={faDonate} heading="Gold & Silver" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4"  className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory(10)}>
        <Card src={faShoppingCart} heading="Grocery Store" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory()}>
        <Card src={faPumpMedical} heading="Medical Stores" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col">
        <Link href="#" onClick={()=>this.handleSubcategory()}>
        <Card src={faMobileAlt} heading="mobile Shopee" />
        </Link>
        </CCol>
        <CCol xs="4" sm="6" md="4" className="card_col">
   <Link href="#"
     onClick={()=>this.handleCat()}
    ><Card src={faEye} heading="View all"/></Link>
        </CCol>
         </CRow>
       </CContainer>
       : "" }
      
       { this.state.cate ?
        
      
          <div>
            
     
          <CRow style={{margin:"-17px -42px 0px"}}>
                
                <CCol md="12" lg="12" xl="12" className="content-center" style={{  backgroundColor:"white",
  marginBottom: "0.5rem"}}>
                   {/* <CLink href="#" onClick={()=>this.back()}> <FontAwesomeIcon icon={faUndoAlt} style={{marginLeft:"-11rem",color:"gray",}}/>  </CLink> */}
                    <h3  style={{color:"rgb(14, 13, 13)"}} >category List</h3>
                 </CCol>
        </CRow>
         
         
         
        { 
         this.state.category.map((item) =>
         
        <Link href="#" onClick={()=>this.handleSubcat(item.id)}> 
        <CateCard catename={item.CatEngName} 
          catImg={"http://badabazzar.jkgroupmanpower.in/public/uploads/Categorys_pic/"+item.CatImage}
         /> 
         </Link> 
         )}
        
        </div>
        
       
      
       :""}
       { this.state.subcate ?
       <div>
       <CRow style={{margin:"-17px -42px 0px"}}>
                <CCol md="12" lg="12" xl="12" className="content-center" style={{  backgroundColor:"white",
  marginBottom: "0.5rem"}}>
  {/*  <CLink href="#" onClick={()=>this.backsub()}> <FontAwesomeIcon icon={faUndoAlt} style={{marginLeft:"-9rem",color:"gray",}}/>  </CLink> */}
                      <h3 style={{color:"rgb(14, 13, 13)"}} >subcategory List</h3>
                 </CCol>
        </CRow>
       { 
         this.state.subcategory.map((item) =>
        
         <Link href="#" onClick={()=>this.handleVendor(item.id)}><Subcate subcatename={item.SubCatEngName} 
           subcatImg={"http://badabazzar.jkgroupmanpower.in/public/uploads/SubCategorys_pic/"+item.SubCatImage}
         />
          </Link> 

       
         )}
       </div>
       :""}
       { this.state.vendor ?
       <div>
       <CRow style={{margin:"-17px -42px 0px"}}>
                <CCol md="12" lg="12" xl="12" className="content-center" style={{  backgroundColor:"white",
  marginBottom: "0.5rem"}}>
  {/* <CLink href="#" onClick={()=>this.backvendor()}> <FontAwesomeIcon icon={faUndoAlt} style={{marginLeft:"-9rem",color:"gray",}}/>  </CLink> */}
                      <h3  style={{color:"rgb(14, 13, 13)"}} >Vendor List</h3>
                 </CCol>
        </CRow>
       { 
         this.state.vendorlist.map((item) =>
        
         <Link href="#" onClick={()=>this.handleproduct(item.id)}><Vendor name={item.DisplayName} 
            CompanyName={item.CompanyName}
          

         
         />
          </Link> 

       
         )}
       </div>
       :""}
       { this.state.product ?
       <div>
       <CRow style={{margin:"-17px -42px 0px"}}>
                <CCol md="12" lg="12" xl="12" className="content-center" style={{  backgroundColor:"white",
  marginBottom: "0.5rem"}}>
  {/* <CLink href="#" onClick={()=>this.backvendor()}> <FontAwesomeIcon icon={faUndoAlt} style={{marginLeft:"-9rem",color:"gray",}}/>  </CLink> */}
                      <h3  style={{color:"rgb(14, 13, 13)"}} >Product List</h3>
                 </CCol>
        </CRow>
       { 
         this.state.productlist.map((item) =>
        
         <Link href="#" onClick={()=>this.handleProductInfo(item.id)}><ProductList name={item.product_name} 
            productImg={"http://badabazzar.jkgroupmanpower.in/public/uploads/Categorys_pic/"+item.product_image}
          

         
         />
          </Link> 

       
         )}
       </div>
       :""}
       { this.state.nearby ?
       <div>
       <CRow style={{margin:"-17px -42px 0px"}}>
                
                <CCol md="12" lg="12" xl="12" className="content-center" style={{  backgroundColor:"white",
  marginBottom: "0.5rem"}}>
                  {/*  <CLink href="#" onClick={()=>this.back()}> <FontAwesomeIcon icon={faUndoAlt} style={{marginLeft:"-11rem",color:"gray",}}/>  </CLink> */}
                    <h3  style={{color:"rgb(14, 13, 13)"}} >Nearby Stores</h3>
                 </CCol>
        </CRow>
          
          
         { 
         this.state.shoplist.map((item) =>
        
         <Link href="#" onClick={()=>this.handleproduct(item.id)}><Nearby name={item.DisplayName} 
            company={item.CompanyName}
            VendorCity={item.VendorCity}
            Address={item.Address}

         
         />
          </Link> 

       
         )}  
       </div>
       :""}
      </div>

      
      
    );
  }
}

export default Location;