import React from 'react';
import {
    CCard,
    CCardBody,
    CCardHeader
  } from '@coreui/react'
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
//   import C3 from "../../assets/img/location.png";
// import { Height } from '@material-ui/icons';
import {Base_URL} from "../../CommonRoutes/Api"

function Card(props) {
    return (
      <CCard style={{marginLeft: "-1rem",
      marginRight: "-1rem"}}>
              <CCardHeader>
              <center><img src={props.src} height="100px" width="100%" /></center>
              </CCardHeader>
              <CCardBody>
                 <center>{props.heading}</center>
              </CCardBody>
            </CCard>
       
    );
}

export default Card;